@import "../styles/variables.scss";

body {
  font-family: "Nunito", sans-serif;
  color: #000;
  font-size: 16px;
  background-color: $background-color;
  overflow-x: hidden;
}

._img-container {
  position: relative;
  overflow: hidden;

  img {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}
